// price

// pagination
.pagination-wrap {
	margin-top: 50px;
	margin-bottom: 50px;

	@media only screen and (min-width : $screen-md) {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		.btn-stake {
			width: 240px;
			margin-right: auto; } } }

.pagination {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin: 2rem 0 0;
	padding: 1px 0 0;
	list-style: none;

	@media only screen and (min-width : $screen-sm) {
		justify-content: flex-start;
		width: max-content;
		max-width: 100%;
		margin-left: auto;
		margin-right: auto; }

	@media only screen and (min-width : $screen-md) {
		margin-top: 6px;
		margin-right: 0; } }

.page-item {
	& + & {
		margin-left: 6px; }

	@media only screen and (min-width : $screen-md) {
		margin-bottom: 6px; } }

.page-link {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	width: 52px;
	height: 52px;
	color: #C7C7C7;
	font-size: 15px;
	line-height: 1.1853em;
	background-color: #fff;
	border: 1px solid #EFEFEF;
	transition: color .2s, border-color .2s;

	.page-item.active &,
	.page-item:hover &,
	&:hover {
		color: #E4536D;
		text-decoration: none;
		border-color: #E4536D; }

	@media only screen and (min-width : $screen-md) {
		width: 32px;
		height: 32px; } }

// category list
.category-list {
	margin: 0;
	padding: 0;
	list-style: none;

	li {
		& + & {
			margin-top: 1rem; }

		&.active {
			a {
				color: #E4536D; } } }

	a {
		font-size: 14px;
		line-height: 1.185em;
		text-transform: uppercase;
		transition: color .2s;

		&:hover {
			color: #E4536D;
			text-decoration: none; } } }


// checkboxes
.checkbox label,
.checkbox input[type="checkbox"],
.checkbox:before,
.checkbox:after {
	cursor: pointer; }

.checkbox {
	position: relative;
	display: block;
	min-height: 24px;
	padding: 3.5px 5px 3.5px 36px;
	line-height: 1;

	label {
		display: inline-block;
		margin: 0;
		font-size: 14px;
		line-height: 1.185em;
		text-transform: uppercase;
		transition: color .2s; }

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		z-index: 0;
		display: block;
		width: 24px;
		height: 24px;
		border: 1px solid #62514B;
		background-color: #fff;
		transition: background-color .2s, border-color .2s; }

	&::after {
		content: '';
		position: absolute;
		top: 4px;
		left: 4px;
		display: block;
		width: 16px;
		height: 16px;
		background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13.3334 4L6.00008 11.3333L2.66675 8' stroke='white' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
		background-size: cover;

		opacity: 0;
		visibility: hidden;
		transition: opacity .25s; }

	&.checked {
		&::before {
			background-color: #E4536D;
			border-color: #E4536D; }

		&::after {
			opacity: 1;
			visibility: visible; } }

	input[type="checkbox"] {
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		width: 24px;
		height: 24px;
		opacity: 0; } }

// radio buttons for Sizes
.radio-list {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin-bottom: 24px;

	@media only screen and (min-width : $screen-lg) {
		margin-bottom: 40px; }

	.radio {
		margin-left: 8px; } }

.radio {
	position: relative;
	display: block;
	width: 61px;
	height: 61px;
	text-align: center;

	label {
		display: block;
		width: 61px;
		height: 61px;
		padding: 21px 12px;
		cursor: pointer;
		font-weight: 500;
		line-height: 1.185em;
		text-align: center;
		text-transform: uppercase;
		background-color: #f8f8f8;
		transition: background-color .2s, color .2s; }

	&.checked,
	&:hover {
		label {
			color: $default-color;
			background-color: $primary-color; } }

	input[type="radio"] {
		position: absolute;
		top: 0;
		left: 0;
		width: 61px;
		height: 61px;
		margin: 0;
		z-index: 1;
		-khtml-opacity: 0;
		-moz-opacity: 0;
		opacity: 0;
		cursor: pointer; } }

// box-group
.box {
	padding: 20px;
	background-color: #FFF;
	box-shadow: 0px 16px 24px rgba(0, 0, 0, 0.06), 0px 2px 6px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);

	p + & {
		margin-top: 24px; }

	@media only screen and (min-width : $screen-lg) {
		padding: 40px;

		p + & {
			margin-top: 40px; } } }

.box-gray {
	padding: 20px;
	background-color: #FAFAFA; }


