// Usufill links
.usufull-link {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: center;
	height: calc(100% - 12px);
	min-height: 90px;
	margin-bottom: 12px;
	padding: 20px 93px 20px 20px;
	background-color: #F8F8F8;

	@media only screen and (min-width : $screen-lg) {
		height: calc(100% - 24px);
		margin-bottom: 24px; }

	&__name {
		font-weight: 500;
		line-height: 1.185em; }

	&__address {
		color: $color-link;
		width: 100%;
		word-wrap: break-word;

		.usufull-link__name + & {
			margin-top: 12px; } }

	&__btn {
		position: absolute;
		top: 50%;
		right: 20px;
		display: block;
		width: 48px;
		height: 48px;
		color: $color-link;
		padding: 12px;
		margin-top: -24px;
		background-color: $default-color;
		transition: color .2s, background-color .2s;

		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: 2;
			display: block; }

		&:hover,
		&:active {
			color: $default-color;
			background-color: $color-link; }

		.svg-icon {
			position: relative;
			z-index: 1;
			width: 24px;
			height: 24px; } } }

.usufull-link-message {
	position: fixed;
	top: 50%;
	left: 50%;
	z-index: 9999;
	display: block;
	width: max-content;
	max-width: 100%;
	font-weight: 500;
	text-transform: uppercase;
	transform: translate(-50%, -50%) scale(.5);
	opacity: 0;
	visibility: hidden;
	animation: 1.1s ease 0s hidden-usufull-message; }

@keyframes hidden-usufull-message {
	from {
		transform: translate(-50%, -50%) scale(.5);
		opacity: 0;
		visibility: hidden; }

	20% {
		transform: translate(-50%, -50%) scale(1);
		opacity: 1;
		visibility: visible; }

	85% {
		transform: translate(-50%, -50%) scale(1);
		opacity: 1;
		visibility: visible; }

	to {
		transform: translate(-50%, -50%) scale(.5);
		opacity: 0;
		visibility: hidden; } }

// Nav Page Links
.nav-page-links__wrap {
	position: relative;
	overflow: hidden;
	z-index: 2; }

.nav-page-links {
	position: relative;
	top: 23px;
	margin-top: -23px;
	padding-bottom: 23px;
	overflow: hidden;
	overflow-x: scroll;

	ul {
		position: relative;
		display: flex;
		flex-wrap: nowrap;
		width: 100%;
		margin: 0;
		padding: 0;
		list-style: none;

		&::before {
			content: '';
			position: absolute;
			left: 0;
			bottom: 0;
			right: 0;
			z-index: 1;
			display: block;
			height: 4px;
			background-color: #F7F7F7; } }

	li {
		position: relative;
		z-index: 2;
		margin: 0;
		padding: 0;
		font-size: 14px;
		line-height: 1.185em;
		text-transform: uppercase; }

	div {
		position: relative;
		display: block;
		margin: 0;
		color: $color-text;
		padding: 20px 60px;
		white-space: nowrap;
		transition: color .2s;
		cursor: pointer;

		&::before {
			content: '';
			position: absolute;
			left: 0;
			bottom: 0;
			right: 0;
			z-index: 2;
			display: block;
			height: 4px;
			background-color: #f6f6f6; }

		&::after {
			content: '';
			position: absolute;
			left: 0;
			bottom: 0;
			z-index: 3;
			display: block;
			width: 0;
			height: 4px;
			background-color: $color-link;
			transition: width .2s; }

		&.active,
		&:hover {
			color: $color-link;
			text-decoration: none;

			&::after {
				width: 100%; } } } }


// price
