		.container {
			position: relative;
			z-index: 3;
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			min-height: 550px;
			padding-top: 50px;
			padding-bottom: 50px; } }

	@media only screen and (min-width : $screen-xl) {

		.container {
			min-height: (700/1920)*100vw; } }

	@media only screen and (min-width : $screen-hd) {

		.container {
			min-height: 700px; } } }


// content
.hero-content {
	position: relative;
	z-index: 5;

	@media only screen and (min-width : $screen-sm) {
		margin-left: 20px;
		margin-right: 20px; }

	@media only screen and (min-width : $screen-lg) {
		flex: 0 0 630px;
		max-width: 630px; }

	&__title {
		margin-bottom: 0;
		color: #FFF;

		&--big {
			@media only screen and (min-width : $screen-lg) {
				font-size: 72px;
				line-height: 1.219027777777778em; } }

		&--small {
			font-size: 24px;
			line-height: 1.219166666666667em;

			@media only screen and (min-width : $screen-lg) {
				font-size: 36px;
				line-height: 1.218888888888889em; } }

		&--color {
			color: $color-link; }

		&--gradient {
			color: linear-gradient(134.79deg, #E34D6F 0%, #E9C54C 111.3%); } }

	&__coins {
		display: none;
		flex-wrap: wrap;
		margin: 28px 0 0;
		padding: 0;
		list-style: none;

		li {
			width: 60px;
			height: 60px;
			margin-right: 24px;

			&:last-child {
				margin-right: 0; } }

		@media only screen and (min-width : $screen-lg) {
			display: flex;
			margin-bottom: 26px; } }

	.btn {
		display: none;

		@media only screen and (min-width : $screen-lg) {
			display: block;
			margin-top: 24px; } } }


// images
.hero-picture {
	display: none;

	@media only screen and (min-width : $screen-lg) {
		position: absolute;
		top: -50px;
		left: (624/1920)*100%;
		right: $grid-gutter-width/2;
		bottom: -50px;
		z-index: 4;
		display: block;
		width: 100vw;
		margin-left: $grid-gutter-width/2;

		img {
			display: block;
			width: 100%;
			height: 100%;
			object-position: 0 0;
			object-fit: contain; } }

	@media only screen and (min-width : $screen-hd) {
		left: 624px; } }

// logos
.hero-logo {
	position: relative;
	z-index: 3;
	height: 318px;
	margin-top: 16px;

	@media only screen and (min-width : $screen-sm) {
		margin-left: 40px;
		margin-right: 40px; }

	@media only screen and (min-width : $screen-lg) {
		position: absolute;
		top: -50px;
		right: 0;
		bottom: -50px;
		left: 0;
		height: auto;
		margin: 0;

		.icon__logo-merchdao {
			position: absolute;
			top: 71px;
			right: (73.66/1920)*100%;
			width: (567.34/1350)*100%;
			height: auto;
			padding-top: (606/1350)*100%;
			background-position: 0 0;

			@media only screen and (min-width : $screen-hd) {
				right: 73.66px;
				width: 567.34px;
				padding-top: 606px; } }

		.icon__logo-merchdao-and-algo {
			position: absolute;
			top: 45px;
			right: -(106.34/1920)*100%;
			width: (978.34/1350)*100%;
			height: auto;
			padding-top: (606/1350)*100%;
			background-position: 0 0;

			@media only screen and (min-width : $screen-hd) {
				right: -106.34px;
				width: 978.34px;
				padding-top: 606px; } } } }

