.header {
	padding-top: 20px;
	padding-bottom: 20px;
	background-color: #fff;

	.container {
		display: flex;
		flex-wrap: wrap;
		align-items: center; }

	@media only screen and (min-width : $screen-md) {
		&--bordered {
			border-bottom: 2px solid #E2E2E2; } }

	@media only screen and (min-width : $screen-xl) {
		padding-top: 40px;
		padding-bottom: 40px; } }

.logo {
	display: block;
	width: 215px;
	margin-right: auto;

	img {
		display: block;
		width: 100%;
		height: auto; }

	@media only screen and (min-width : $screen-xl) {
		width: 375px; } }

.navigation {
	display: none;
	margin-left: 40px;

	ul {
		display: flex;
		flex-wrap: wrap;
		margin: 0;
		padding: 0;
		list-style: none; }

	li + li {
		margin-left: 51px; }

	a {
		color: $color-text;
		font-size: 14px;
		line-height: 1.185em;
		text-transform: uppercase;
		transition: color .2s;

		&:hover {
			color: $color-link;
			text-decoration: none; } }

	@media only screen and (min-width : $screen-xl) {
		display: block; } }

.social {
	display: none;
	flex-wrap: wrap;
	margin: 0 0 0 40px;
	padding: 0;
	list-style: none;

	li + li {
		margin-left: 10px; }

	&__item {
		display: block;
		width: 36px;
		height: 36px;
		color: $color-text;
		transition: color .2s;

		&:hover {
			color: $color-link;
			text-decoration: none; } }

	@media only screen and (min-width : $screen-xl) {
		display: flex; } }

.btn-burger {
	display: block;
	width: 24px;
	height: 24px;
	margin-left: 15px;

	@media only screen and (min-width : $screen-xl) {
		display: none; } }

.ReactModal__Overlay {
	z-index: 10; }
