.home-page-bg {
	@media only screen and (min-width : $screen-lg) {

		&::before {
			content: '';
			position: absolute;
			top: 151px;
			right: 0;
			z-index: -1;
			display: block;
			width: (1224/1920)*100vw;
			height: (1292/1920)*100vw;
			background: url('../../assets/Images/bg__right-top.png') 0 0 no-repeat;
			background-size: cover;

			@media only screen and (min-width : $screen-hd) {
				width: 1224px;
				height: 1292px; } }

		&::after {
			content: '';
			position: absolute;
			top: (1651/1920)*100vw;
			left: 0;
			z-index: -1;
			display: block;
			width: (857/1920)*100vw;
			height: (1784/1920)*100vw;
			background: url('../../assets/Images/bg__left-middle.png') 0 0 no-repeat;
			background-size: cover;

			@media only screen and (min-width : $screen-hd) {
				top: 1651px;
				width: 857px;
				height: 1784px; } } }

	> * {
		z-index: 2; } }

